body {
  background-color: black;
  /* height: 200vh; */
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

.navBar {
  background-color: rgb(20, 20, 20);
  opacity: 0.9;
  /* border-bottom: 1px solid rgb(255, 255, 255); */
  height: 8vh;
  width: 100%;
  position: fixed;
  display: flex;
  align-items: center;
  top: 0%;
  /* box-shadow: rgba(255, 255, 255, 0.45) 0px 25px 20px -20px; */
}
.navBar p {
  color: white;
  font-family: sans-serif;
  font-weight: 200;
  font-size: 1vw;
}

.links{
  /* border: 1px solid blue; */
  display: flex;
  width: 60%;
  height: 100%;
  margin-left: 10%;
  justify-content: center;
  gap: 3vw;
  align-items: center;
}

.name {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 3vw;
  font-family: sans-serif;
  width: 20%;
  height: 50%;
  text-align: center;
  font-size: 2vw;
  font-weight: 300;
  /* color: #032cfc; */
  color: rgb(90, 189, 255);
}
.name span {
  color:#f403fc;
}

.brainIntro {
  /* border-top: 1px solid blue; */
  width: 100vw;
  height: 90vh;
  margin-top: 9vh;
}
.leftBrain {
  border-top: 1px solid rgb(217, 255, 0);
  border-bottom: 1px solid rgb(217, 255, 0);
  width: 100vw;
  height: 90vh;
  margin-top: 50vh;
}
.rightBrain {
  border-top: 1px solid blue;
  /* border-bottom: 1px solid blue; */
  width: 100vw;
  height: 90vh;
  /* margin-top: 7vh; */
}

footer {
  border-top: 1px solid white;
  width: 100%;
  height: 10vh;
  margin-top: 100px;
  background-color: rgb(20, 20, 20);
}